<template>
  <div class="layout">
    <div class="left">
      <div class="logo fs14">
        <img src="../../assets/logo.jpg" alt="logo">
        FiveXuUIPlus
      </div>
      <div class="menu">
        <fx-menu :default-active="defaultRoute" router>
          <template v-for="item in menuList" :key="item.router">
            <fx-menu-item :index="item.router">{{ item.name }}</fx-menu-item>
          </template>
        </fx-menu>
      </div>
    </div>
    <div class="right">
      <header class="header">
        <fx-autocomplete v-model="search" class="search" round width="300" @mouseenter.stop @mouseleaves.stop
                         @mousedown.stop placeholder="搜素组件" :fetch="searchComponents" searchKey="name" clear
                         @select="select"></fx-autocomplete>
      </header>
      <main>
        <router-view class="router"/>
      </main>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, reactive } from 'vue'
import MenuList, { MenuListProps } from '@/assets/menuList'
import { useRouter } from 'vue-router'

export default defineComponent({
  name: 'layout',
  setup () {
    const router = useRouter()
    const isDev = process.env.NODE_ENV === 'development'
    const defaultRouter: string = isDev ? MenuList[MenuList.length - 1].router : MenuList[0].router
    const defaultRoute = ref<string>(defaultRouter)

    // 菜单列表
    const menuList = reactive(MenuList)
    // 搜索内容
    const search = ref<string>('')
    // 搜索时间
    const searchComponents = (searchText: string, cb: (val: MenuListProps[]) => void) => {
      const results = searchText ? MenuList.filter((item: MenuListProps) => {
        return item.name.match(searchText)
      }) : MenuList
      cb(results)
    }
    const select = (data: MenuListProps): void => {
      router.push(data.router)
    }

    return {
      search,
      menuList,
      select,
      searchComponents,
      defaultRoute
    }
  }
})
</script>

<style scoped lang="scss">
@import "../../assets/scss/common";

$layout-height: 50px;

.layout {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;

  > .left {
    width: 160px;
    height: 100%;
    border-right: 1px solid #ccc;

    > .logo {
      height: $layout-height;
      line-height: $layout-height;
      display: flex;
      box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);

      > img {
        margin-top: 5px;
        margin-left: 10px;
        margin-right: 10px;
        height: 40px;
        object-fit: contain;
      }
    }

    .menu {
      width: 100%;
      height: calc(100% - #{$layout-height});
      margin: 10px 0;
      box-sizing: border-box;
    }
  }

  > .right {
    width: calc(100% - 150px);
    height: 100%;

    > header {
      height: $layout-height;
      box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);

      > .search {
        margin-top: 9px;
        margin-left: 50px;
        position: relative;
        z-index: 10;
      }
    }

    > main {
      width: 100%;
      height: calc(100% - #{$layout-height});
      padding: 20px;
      box-sizing: border-box;
      background-color: #eee;

      > .router {
        width: 100%;
        height: 100%;
        overflow-y: auto;
      }
    }
  }
}
</style>
